<template>
    <div class="wrapper">
        <h1>Редагувати запис: {{article.title}}</h1>
			<form @submit.prevent="update(article.id, item)">
				<div class="row">
					<div class="col-12">
						<div class="form-group">
						    <label for="title">Назва запису</label>
						    <input type="text" v-model='item.title' class="form-control" id="title">
						</div>
					</div>
					<!-- /.col-12 col-lg-6 -->					
					<div class="col-12">
						<div class="form-group">
					    	<label for="pre">Прев'ю запису:</label>
					    	<PreviewImage @getImg='getSrc' :currentImage='article.img'/>
					  	</div>
					</div>
					<!-- /.col-12 -->				
				</div>
				<div class="form-group">
			    	<label for="excerpt">Короткий опис:</label>
			    	<textarea name="excerpt" class="form-control" v-model='item.excerpt' id="excerpt"></textarea>
			  	</div>
			  	<div class="form-group">
			    	<label for="describe">Контент:</label>
					<vue-editor for='describe' v-model="item.content"></vue-editor>
			  	</div>
				
				<button class="btn btn-primary" type="submit">Оновити запис</button>
			</form>	
			
    </div>
</template>
<script>
	import { db } from '@/main';

	import { VueEditor } from 'vue2-editor';
	import PreviewImage from '@/components/PreviewImage';
	export default {
		name: 'NewPost',
        props: ['article'],
		data() {
			return {
				item: {
					title: this.article.title,
					img: this.article.img,
					excerpt: this.article.excerpt,
					content: this.article.content
				},		
			}
		},
		components: {
		      VueEditor,
		      PreviewImage
		   },
		methods: {
		    update(id,item) {
                const createdAt = new Date();
		        db.collection('blog').doc(id).update({ ...item, createdAt })
		   },
		    getSrc (data) {
		        this.item.img = data;
		    },
		 }
	}
</script>
<style scoped>
	input, select, textarea {
		display: block;
	}
</style>